import React from "react";
import Spinner from '../../widgets/Spinner';

const token_key = 'es_token';

class Comp extends React.Component {
  constructor() {
    super();
    this.state = {
      token: localStorage[token_key],
      loading: true
    }
  }

  componentDidMount() {
    this.load();
  }

  load() {
    this.setState({loading: true});

    const {token} = this.state;
    fetch('/logically/browsing', {headers: {token}})
      .then(res => res.json())
      .then(data => {
        this.setState({
          loading: false,
          data,
        })
      });
  }

  render() {
    const {loading, data} = this.state;
    if (loading) {
      return <div className="container">
        <Spinner/>
      </div>
    }
    if (data.ok === false) {
      return <div className="container">
        <strong style={{color: 'red'}}>{data.message}</strong>
      </div>
    }
    return <div className="container">
      <h3>Total: {data.total}</h3>
      <table className="table is-striped" style={{tableLayout: 'fixed'}}>
        <thead>
        <tr>
          <th>SN</th>
          <th>Domain</th>
          <th>Count</th>
          <th>First</th>
          <th>Last</th>
        </tr>
        </thead>
        <tbody>
        {data.buckets.map((v, i) => {
          return <tr key={v.key}>
            <td>{i + 1}</td>
            <td>
              <div title={v.key} style={{
                textOverflow: 'ellipsis',
                maxWidth: '20em',
                whiteSpace: 'nowrap',
                overflow: 'hidden'
              }}>{v.key}</div>
            </td>
            <td>{v.doc_count}</td>
            <td>{new Date(v.min_date.value).toLocaleString()}</td>
            <td>{new Date(v.max_date.value).toLocaleString()}</td>

          </tr>
        })}
        </tbody>
      </table>
    </div>
  }
}

export default Comp;
